@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    background-color: v.$pantone4
    ;
    color: v.$blanco;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    background: rgb(239, 239, 242);
    background: linear-gradient(90deg, rgba(239, 239, 242, 1) 4%, rgba(122, 122, 122, 1) 100%, rgba(37, 42, 43, 1) 100%);
    //background-image: linear-gradient(90deg, rgba(188,149,92,1) 0%, rgba(84,86,90,1) 0%, rgba(210,203,193,1) 100%, rgba(195,165,123,1) 100%);
    height: auto;
    background-size: cover;
    background-size: auto;
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    font-size: 1.6rem;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}
