@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app{
    @include m.tablet{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
    }

    .imagen {
        background-image: url(../img/xolosveterinaria.png);
        background-size: 75%;
        background-position: center center;
        height: 30rem;
        background-repeat: no-repeat;
        @include m.tablet{
            height: auto;
            background-position: center center;
            background-size: 75%;
        }
    }

    .app{
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0;
    }
}

.barra {
    margin: v.$separacion 0;
    @include m.tablet{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }



    .boton{
        @include m.boton(v.$negro, v.$blanco);
        margin: 0;
    }
}


.barra-servicios {
    margin-bottom: v.$separacion;

    @include m.tablet{
        display: flex;
        justify-content: space-between;
    }

    .boton {
        margin: 0 0 1rem 0;
    }
}