@use 'base/variables' as v;
@use 'base/mixins' as m;

.tabs {

    @include m.tablet{
        display: flex;
    }

    button {
        background-color: v.$gris2;
        border: none;
        color: v.$blanco;
        padding: 1rem;
        display: block;
        width: 100%;
        margin-bottom: 1rem;
        text-transform: uppercase;
        font-weight: bold;
        transition: background-color 300ms;

        &:hover{
            cursor: pointer;
            background-color: darken(v.$verde, 10%);
        }


    }
}