@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-servicios {

    @include m.tablet {
        display: grid;
        gap: 2rem;
        @include m.tablet{
            @include m.grid(2, 2rem)
        }
    }
    .servicio{
        background-color: v.$pantone3;
        padding: 3rem;
        border-radius: 1rem;
        transition-property: transform;
        transition-duration: 300ms;
        text-align: center;

        &.seleccionado {
            background-color: v.$pantone;
            p{
                color: v.$blanco;
            }
        }

        
        &:hover {
            transform: scale(1.1);
        }

        p{
            color: v.$negro;
            margin: 0 0 calc(v.$separacion/4) 0;
        }

        .precio-servicio {
            color: v.$blanco;
            font-weight: v.$bold;
            font-size: 3rem;
            margin: 0;
        }

    }

}